<template>
  <div>
    <div class="search-container">
      <el-input
      class="search-input"
      placeholder="..."
      size="mini"
      prefix-icon="el-icon-search"
      v-model="keyWords"></el-input>
    </div>
    <div>
      <el-tree
      class="tree-style"
      :props="props"
      :data="treeData"
      node-key="id"
      ref="tree"
      :filter-node-method="filterNode"
      highlight-current
      :render-content="renderContent"
      v-loading="treeLoading">
      </el-tree>
      <div class="dialog-container">
        <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="40%">
          <el-form :model="menuForm" ref="menuForm" :rules="rules" label-position="top" label-width="120px" v-loading="formLoading">
            <el-form-item prop="url" label="url">
              <el-input size="mini" v-model="menuForm.url" placeholder="url..."></el-input>              
            </el-form-item>
            <el-form-item prop="name" label="">
              <el-input size="mini" v-model="menuForm.name" placeholder="..."></el-input>              
            </el-form-item>
            <el-form-item prop="component" label="">
              <el-input size="mini" v-model="menuForm.component" placeholder="..."></el-input>              
            </el-form-item>
            <el-form-item prop="path" label="path">
              <el-input size="mini" v-model="menuForm.path" placeholder="..."></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input size="mini" v-model="menuForm.iconCls" placeholder="font-awesome..."></el-input>              
            </el-form-item>
            <div class="check-container">
              <el-checkbox v-model="menuForm.keepAlive"></el-checkbox>
              <el-checkbox v-model="menuForm.requireAuth"></el-checkbox>
            </div>
          </el-form>
          <span slot="footer">
            <el-button size="samll" @click="cancelAddMenu('menuForm')"></el-button>
            <el-button size="samll" type="primary" @click="addMenu('menuForm')"></el-button>
          </span>
        </el-dialog>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'MenuMana',
    data() {
      return {
        keyWords: '',
        treeLoading: false,
        treeData: [],
        props: {
          children: 'children',
          label: 'name'
        },
        dialogVisible: false,
        currentNode: {},
        currentNodeData: {},
        formLoading: false,
        menuForm: {
          url: '',
          path: '',
          component: '',
          name: '',
          iconCls: '',
          keepAlive: false,
          requireAuth: false
        },
        rules: {
          url: [
            { required: true, message: 'url', trigger: 'blur' }
          ],
          path: [
            { required: true, message: '', trigger: 'blur' }
          ],
          component: [
            { required: true, message: '', trigger: 'blur' }
          ],
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]                    
        }
      }
    },
    watch: {
      keyWords (val) {
        //  val  filterNode (value, data) 
        this.$refs.tree.filter(val)
      }
    },
    mounted() {
      this.loadAllMenus()
    },
    methods: {
      loadAllMenus () {
        this.treeLoading = true
        var _this = this
        this.getRequest("/system/basic/allMenusTree")
        .then(resp => {
          _this.treeLoading = false
          if(resp && resp.status == 200) {
              _this.treeData = resp.data.obj
          }
        })
      },
      /**
       * @param node  Node 
       * @param data 
       */
      renderContent (h, {node, data, store}) {
        return (
          <span style="flex:1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;">
            <span>{node.label}</span>
            <span>
              <el-button style="font-size: 12px; padding: 3px" type="primary" size="mini" on-click={ () => this.showAddMenuView(data,event) }></el-button>
              <el-button style="font-size: 12px; padding: 3px" type="danger" size="mini" on-click={ () => this.deleteMenu(node,data,event) }></el-button>
            </span>
          </span>
        )
      },
      /**
       * @param value this.$refs.tree.filter(val)
       * @param data  this.treeData
       * @returns true false
       */
      filterNode (value, data) {
        // 
        if (!value) return true
        // namevalue
        return data.name.indexOf(value) !== -1
      },
        // data
      showAddMenuView(data,event) {
        this.dialogVisible = true
        this.currentNodeData = data
        // 
        event.stopPropagation()
      },
        //node Node   data
        /**
         * 
         *      children[]  children[] 
         *  id  id 
         */
      deleteMenu(node,data,event) {
        var _this = this
        this.$confirm('[' + data.name + ']', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          var menuId = data.id
          _this.formLoading = true
          this.deleteRequest('/system/basic/menu/' + menuId)
          .then(resp => {
            if (resp && resp.status == 200) {
              // 
              const parent = node.parent
              const children = parent.data.children || parent.data
              const index = children.findIndex(d => d.id === menuId)
              children.splice(index, 1)
            }
          })
        }).catch(() => {
          _this.$message({
            type: 'info',
            message: ''
          })
        })
        // 
        event.stopPropagation()
      },
      addMenu (formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            var parentId = this.currentNodeData.id
            var _this = this
            this.formLoading = true
            this.postRequest('/system/basic/menu', {
              url: this.menuForm.url,
              path: this.menuForm.path,
              component: this.menuForm.component,
              name: this.menuForm.name,
              iconCls: this.menuForm.iconCls,
              meta: {
                keepAlive: this.menuForm.keepAlive,
                requireAuth: this.menuForm.requireAuth
              },
              parentId: parentId
            }).then(resp => {
              _this.formLoading = false
              if (resp && resp.status == 200) {
                // dialog
                _this.dialogVisible = false

                var newMenu = resp.data.obj

                //
                if (!this.currentNodeData.children) {
                  //  children 
                  this.$set(this.currentNodeData, 'children', [])
                }
                this.currentNodeData.children.push(newMenu)

                // (dialog)
                if (_this.$refs[formName] !== undefined) {
                  _this.$refs[formName].resetFields()
                }
              }
            })
          } else {
            return false
          }
        })
      },
      cancelAddMenu (formName) {
        this.dialogVisible = false
        this.$refs[formName].clearValidate()
      }
    }
  };
</script>
<style lang="stylus" scoped>
  .search-container
    text-align left 
    .search-input
      width 500px
      margin 0
      padding 0
  .tree-style
    width 500px
    margin-top 10px
  .dialog-container
    text-align left
    .check-container
      display flex
      justify-content center
      margin-top 10px
</style>