<template>
    <div class="sysuser-container" v-loading="loading">
        <div class="header-container">
            <el-input 
            class="search-content"
            placeholder="..."
            prefix-icon="el-icon-search"
            size="small"
            v-model="keyWords"></el-input>
            <el-button size="small" type="primary" icon="el-icon-search" @click="searchClick"></el-button>
            <el-button style="margin-left:10px;" size="small" type="primary" icon="el-icon-plus" @click="addUserDialogShow"></el-button>
        </div>
        <div class="body-container">
            <div class="card-container" v-for="(user,index) of users" :key="user.id">
                <el-card class="user-card" v-loading="cardLoadings[index]">
                    <div slot="header" class="clearfix">
                        <span>{{user.name}}</span>
                        <el-button class="button" type="text" icon="el-icon-delete" @click="deleteUser(user.id)"></el-button>
                        <el-button class="button" type="text" icon="el-icon-edit" @click="showUpdateUserView(user)" style="margin-right: 10px"></el-button>
                    </div>
                    <div>
                        <div class="img-container">
                            <img class="user-img" :src="user.userface" :alt="user.name"/>
                        </div>
                        <div class="info-container">
                            <span class="user-info">{{user.name}}</span>
                            <span class="user-info">{{user.phone}}</span>
                            <span class="user-info">{{user.telephone}}</span>
                            <span class="user-info">{{user.address}}</span>
                            <div class="user-info user-state">
                                
                                <el-switch
                                class="switch"
                                v-model="user.enabled"
                                active-color="#13ce66"
                                inactive-color="#aaaaaa"
                                active-text=""
                                inactive-text=""
                                :key="user.id"
                                @change="switchChange(user.enabled,user.id,index)">
                                </el-switch>
                            </div>
                            <div class="user-info">
                                
                                <el-tag
                                class="role-tag"
                                v-for="role of user.roles"
                                :key="role.id"
                                type="success"
                                size="mini"
                                :disable-transitions="false">
                                    {{role.nameZh}}
                                </el-tag>
                                <el-popover
                                v-loading="epLoadings[index]"
                                placement="right"
                                title=""
                                width="200"
                                trigger="click"
                                @hide="updateUserRoles(user.id,index)">
                                    <el-select 
                                    v-model="selectedRoles"
                                    multiple
                                    placeholder="">
                                        <el-option
                                        v-for="ar in allRoles"
                                        :key="ar.id"
                                        :label="ar.nameZh"
                                        :value="ar.id">
                                        </el-option>
                                    </el-select>
                                    <!-- slot="reference" popover -->
                                    <el-button class="more-button" type="text" icon="el-icon-more" slot="reference" 
                                    @click="loadSelectedRoles(user.roles,index)" :disabled="moreBtnState"></el-button>
                                </el-popover>
                            </div>
                            <span class="user-info">{{user.remark}}</span>
                        </div>
                    </div>
                </el-card>                
            </div>
        </div>
        <div v-if="total>pageSize" class="foot-container">
            <el-pagination
            background
            :page-size="pageSize"
            :current-page="pageNum"
            @current-change="handleCurrentChange"
            layout="total, prev, pager, next, jumper"
            :page-count="7"
            :total="total"></el-pagination>
        </div>
        <div class="dialog-container">
            <el-dialog
            :title="dialogTitle"
            :visible.sync="dialogVisible"
            width="50%">
                <el-form 
                :model="userForm" 
                ref="userForm" 
                :rules="rules"
                status-icon 
                label-width="100px" 
                label-position="right" 
                v-loading="formLoading">
                    <el-row>
                        <el-col :span="7">
                            <div>
                                <el-form-item prop="name" label="">
                                    <el-input prefix-icon="el-icon-edit" v-model="userForm.name" size="mini" placeholder="..."></el-input>
                                </el-form-item>
                            </div>
                        </el-col>
                        <el-col :span="8" :offset="1">
                            <div>
                                <el-form-item prop="phone" label="">
                                    <el-input prefix-icon="el-icon-mobile-phone" v-model="userForm.phone" size="mini" placeholder="..."></el-input>
                                </el-form-item>
                            </div>
                        </el-col>
                        <el-col :span="7" :offset="1">
                            <div>
                                <el-form-item prop="telephone" label="">
                                    <el-input prefix-icon="el-icon-phone" v-model="userForm.telephone" size="mini" placeholder="..."></el-input>
                                </el-form-item>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="24">
                            <div>
                                <el-form-item prop="address" label="">
                                    <el-input prefix-icon="el-icon-edit" v-model="userForm.address" size="mini" placeholder="..."></el-input>
                                </el-form-item>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="24">
                            <div>
                                <el-form-item prop="username" label="">
                                    <el-input prefix-icon="el-icon-edit" v-model="userForm.username" size="mini" placeholder="..."></el-input>
                                </el-form-item>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="11">
                            <div>
                                <el-form-item prop="password" label="">
                                    <el-input prefix-icon="el-icon-edit" v-model="userForm.password" show-password size="mini" placeholder="..."></el-input>
                                </el-form-item>
                            </div>
                        </el-col>
                        <el-col :span="11" :offset="2">
                            <div>
                                <!-- v-model  prop -->
                                <el-form-item prop="checkPass" label="">
                                    <el-input prefix-icon="el-icon-edit" v-model="userForm.checkPass" show-password size="mini" placeholder="..."></el-input>
                                </el-form-item>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="24">
                            <div>
                                <el-form-item label="">
                                    <el-input prefix-icon="el-icon-edit" v-model="userForm.remark" size="mini" placeholder="..."></el-input>
                                </el-form-item>
                            </div>
                        </el-col>
                    </el-row>
                </el-form>
                <span slot="footer">
                    <el-button size="samll" @click="cancel('userForm')"></el-button>
                    <el-button size="samll" type="primary" @click="editUser('userForm')"></el-button>
                </span>
            </el-dialog>
        </div>
    </div>
</template>
<script>
export default {
    name: "SysUser",
    data() {
        var validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''))
            } else if (value !== this.userForm.password) {
                callback(new Error(''))
            } else {
                callback()
            }
        }
        return {
            loading: false,
            keyWords: '',
            pageNum: 1,
            pageSize: 6,
            total: 0,
            users: [],
            allRoles: [],
            // 
            cardLoadings: [],
            // pop
            epLoadings: [],
            //
            selectedRoles: [],
            //
            oldRoles: [],
            // 
            moreBtnState: false,
            currentPage: 1,
            dialogVisible: false,
            dialogTitle: '',
            rules: [],
            formLoading: false,
            userForm: {
                name: '',
                phone: '',
                telephone: '',
                address: '',
                username: '',
                password: '',
                userface: '',
                remark: '',
                checkPass: ''
            },
            rules: {
                name: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                phone: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 11, max: 11, message: '11', trigger: 'blur' }
                ],
                telephone: [
                    { required: false, message: '', trigger: 'blur' },
                ],
                address: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                username: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                password: [
                    { required: false, message: '', trigger: 'blur' }
                ],
                checkPass: [
                    { validator: validatePass, trigger: 'blur'}
                ]
            }
        }
    },
    methods: {
        /**
         * @param keyWords 
         * @param pageNum 
         */
        search (keyWords, pageNum) {
            this.loading = true
            var _this = this
            var searchWords
            if (this.keyWords === '') {
                searchWords = 'all'
            } else {
                searchWords = this.keyWords
            }
            this.getRequestWithParams('/system/user/' + searchWords, {
                pageNum: pageNum,
                pageSize: this.pageSize
            }).then(resp => {
                _this.loading = false
                if(resp && resp.status == 200) {
                    _this.users = resp.data.obj.users
                    _this.total = resp.data.obj.total
                    var length = resp.data.obj.users.length
                    // length[false, false, false...]
                    _this.cardLoadings = Array(null, Array(length)).map(function (item, i) {
                        return false
                    })
                    _this.epLoadings = Array(null, Array(length)).map(function (item, i) {
                        return false
                    })
                }
            })
        },
    
        searchClick () {
            this.search(this.keyWords, 1)
        },
        addUserDialogShow () {
            this.dialogVisible = true
            this.dialogTitle = ''
        },
        initUserCards () {
            this.search(this.keyWords, 1)
        },
        loadAllRoles () {
            var _this = this
            this.getRequest("/system/basic/roles")
            .then(resp => {
                if (resp && resp.status == 200) {
                    _this.allRoles = resp.data.obj
                }
            })
        },
        showUpdateUserView (user) {
            this.userForm = user
            this.dialogVisible = true
            this.dialogTitle = ''
        },
        deleteUser (userId) {
            var _this = this
            this.loading = true
            this.deleteRequest("/system/user/" + userId)
            .then(resp => {
                _this.loading = false
                // 
                if (resp && resp.status == 200 && resp.data.status == 200) {
                    _this.initUserCards()
                }
            })
        },
        switchChange(enabled,userId,index) {
            // true
            this.cardLoadings.splice(index, 1, true)
            var _this = this
            this.putRequest('/system/user/', {
                id: userId,
                enabled: enabled
            }).then(resp => {
                _this.cardLoadings.splice(index, 1, false)
                if (resp && resp.status == 200) {
                    // switch
                    if (resp.data.status == 500) {
                        _this.refreshUser(userId,index)
                    }
                } else {
                    _this.refreshUser(userId,index)
                }
            })
        },
        refreshUser (userId,index) {
            this.cardLoadings.splice(index, 1, true)
            var _this = this
            this.getRequest("/system/user/id/" + userId)
            .then(resp => {
                this.cardLoadings.splice(index, 1, false)
                if (resp && resp.status == 200) {
                    var user = resp.data.obj
                    // 
                    _this.users.splice(index, 1, user)
                }
            })
        },
        updateUserRoles (userId,index) {
            //
            this.moreBtnState = false
            //
            //    
            if (this.selectedRoles.length == this.oldRoles.length) {
                for (let i = 0; i < this.selectedRoles.length; i++) {
                    for (let j = 0; j < this.oldRoles.length; j++) {
                        if (this.selectedRoles[i] == this.oldRoles[j]) {
                            this.oldRoles.splice(j,1)
                            break
                        }
                    }
                    //  this.oldRoles this.selectedRolesi
                    // this.oldRoles,
                    if (this.selectedRoles.length == this.oldRoles.length) {
                        break
                    }
                }
            }
            //
            if (this.oldRoles.length == 0) {
                return
            }
            // 

            this.epLoadings.splice(index, 1, true)
            var _this = this
            this.putRequest("/system/user/roles", {
                userId: userId,
                roleIds: this.selectedRoles
            }).then(resp => {
                _this.epLoadings.splice(index, 1, false)
                // 
                if (resp && resp.status == 200 && resp.data.status == 200) {
                    _this.refreshUser(userId,index)
                }
            })
        },
        loadSelectedRoles(roles,index) {
            this.moreBtnState = true
            this.selectedRoles = []
            this.oldRoles = []
            roles.forEach(role => {
                //  <el-select>,<el-option>id:value="ar.id"
                this.selectedRoles.push(role.id)
                this.oldRoles.push(role.id)
            })
        },
        handleCurrentChange (currentPage) {
            //  currentPage  this.pageNum
            this.search(this.keyWords, currentPage)
        },
        cancel (formName) {
            this.dialogVisible = false
        },
        editUser (formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.formLoading = true
                    var _this = this
                    if (this.userForm.id) {
                        this.putRequest('/system/user/', this.userForm)
                        .then(resp => {
                            _this.handleResponse(_this, resp, formName)
                        })
                    } else {
                        this.postRequest('/system/user/', this.userForm)
                        .then(resp => {
                            _this.handleResponse(_this, resp, formName)
                        })
                    }
                } else {
                    return false
                }
            })
        },
        handleResponse (context, resp, formName) {
            context.formLoading = false
            if(resp && resp.status == 200 && resp.data.status == 200) {
                context.initUserCards()
                context.dialogVisible = false
                
                // (dialog)
                if (context.$refs[formName] !== undefined) {
                    context.$refs[formName].resetFields()
                }
                context.emptyUser()
            }
        },
        emptyUser () {
            this.userForm = {
                name: '',
                phone: '',
                telephone: '',
                address: '',
                username: '',
                password: '',
                userface: '',
                remark: '',
                checkPass: ''
            }
        }
    },
    mounted() {
        this.initUserCards()
        this.loadAllRoles()
    },
}
</script>
<style lang="stylus" scoped>
    .sysuser-container
        margin-top 10px
        .header-container
            display flex
            justify-content center
            align-items center
            margin-bottom 20px
            .search-content
                width 400px
                margin-right 10px
        .body-container
            display flex
            justify-content space-around
            // 
            flex-wrap wrap
            text-align left
            width 100%
            .card-container
                flex 0 0 33%
                display flex
                justify-content center
                .user-card
                    width 350px
                    margin-bottom 20px
                    .button
                        float right
                        padding 3px 0
                        margin 0
                        width 15px
                        height 15px
                        color #f6061b
                    .img-container
                        display flex
                        justify-content center
                        width 100%
                        .user-img
                            width 70px
                            height 70px
                            border-radius 70px
                    .info-container
                        margin-top 20px
                        display flex
                        flex-direction column
                        .user-info
                            font-size 14px
                            color #09c0f6
                            .role-tag
                                margin-right 5px
                            .more-button
                                color #09c0f6
                                padding-top 0px
                        .user-state
                            display flex
                            align-items center
                            margin-bottom 3px
                            .switch
                                display inline
                                margin-left 5px
        .foot-container
            display flex
            justify-content center
            margin-top 10px
        .dialog-container
            text-align left
</style>
